import React from 'react';
import { cssDecorator } from '@front-toolkit/utils';

import { YearValue } from '../../types';

import commonStyles from '../../styles/common.scss';

const cdCommon = cssDecorator(commonStyles);

export interface YearGridProps {
  /** Годы для отображения */
  items: YearValue[];

  /** Выбранный год изменился */
  onChange: (value: YearValue, index?: number) => void;
}

/** Сетка годов */
export function YearsGrid({
  items,
  onChange
}: YearGridProps): JSX.Element {
  const years = items.map((year, index) => {
    const classes = cdCommon(
      'cell',
      {
        hover: !year.disabled && !year.selected,
        disabled: year.disabled,
        selected: year.selected,
        today: year.today
      },
      [commonStyles.button]
    );

    return (
      <button
        type="button"
        key={ `year${ year.value }` }
        className={ classes }
        tabIndex={ -1 }
        onClick={ (): void => {
          if (!year.disabled) onChange(year, index);
        } }
      >
        { year.value }
      </button>
    );
  });

  return (
    <div
      className={ commonStyles['grid-column4'] }
    >
      { years }
    </div>
  );
}
